html {
  font-size: 100%;
  box-sizing: border-box;
  overflow-x: hidden;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-size: 1rem;
  background: var(--vrylightgray);
  font-family: var(--font1);
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --lightpurple: hsl(243, 78%, 82%);
  --hotpink: hsl(338, 100%, 69%);
  --offsetblue: hsl(240, 100%, 73%);
  --paleblue: hsl(225, 100%, 94%);
  --brightblue: hsl(245, 75%, 52%);
  --brightred: hsl(0, 83%, 49%);
  --vrypaleblue: hsl(225, 100%, 98%);
  --desturatedblue: hsl(224, 23%, 55%);
  --darkblue: hsl(225, 46%, 20%);
  --offwhite: hsl(225, 50%, 98%);
  --vrydarkblue: hsl(226, 43%, 10%);
  --purple: hsl(246, 80%, 60%);
  --paleblue: hsl(236, 100%, 87%);
  --lightorange: hsl(15, 100%, 70%);
  --softblue: hsl(195, 74%, 62%);
  --lightred: hsl(348, 100%, 68%);
  --green: hsl(145, 58%, 55%);
  --violet: hsl(264, 64%, 52%);
  --yellow: hsl(43, 84%, 65%);
  --brightorange: hsl(31, 77%, 52%);
  --darkcyan: hsl(184, 100%, 22%);
  --vrydarkcyan: hsl(179, 100%, 13%);
  --vrylightgray: hsl(0, 0%, 95%);
  --transparentwht: hsla(0, 0%, 100%, 0.75);
}

:root {
  --font1: "Lexend Deca", sans-serif;
  --font2: "Big Shoulders Display", cursive;
}

.c1 {
  color: var(--vrylightgray);
  background-color: var(--brightorange);
  padding: 3.125rem;
  border-radius: 10px;
}
.c1__icon {
  padding-bottom: 1.875rem;
}
.c1__title {
  font-family: var(--font2);
  text-transform: uppercase;
  padding-bottom: 1.5625rem;
}
.c1__about {
  color: var(--transparentwht);
  padding-bottom: 1.25rem;
}
.c1__btn {
  color: var(--brightorange);
  background-color: var(--vrylightgray);
  border: 2px solid var(--vrylightgray);
  font-family: var(--font1);
  font-size: 1rem;
  padding: 0.75rem 1.8125rem;
  border-radius: 1.875rem;
}
.c1__btn:hover {
  background-color: transparent;
  color: var(--transparentwht);
  text-decoration: none;
  cursor: pointer;
}

